

import React, { Component, useState } from 'react';
import { connect } from 'react-redux'
import { Select } from 'antd'
import Form from '../../../../../components/form/form';
import Dropdown from '../../../../../components/form/form-controls/select';
import Button from '../../../../../components/form/button/button';
import Loader from '../../../../../components/ui/loader/loader';
import ErrorBox from '../../../../../components/form/error-box/error-box';
import * as actionType from '../../../../../store/actions/action-type'
import { GetToken } from '../../../../../helpers/token';
import { addProduct } from '../../../../../services/api/orders';

const Option = Select.Option;

const AddProduct = (props) => {

    const [productId, setproductId] = useState('');
    const [name, setname] = useState('');
    const [version, setversion] = useState('');
    const [machineSRN, setmachineSRN] = useState('');
    const [serialNumber, setserialNumber] = useState('');
    const [errorMsg, seterrorMsg] = useState(false);
    const [showLoader, setshowLoader] = useState(false);
    const [serialNumberWarn, setserialNumberWarn] = useState(false);
    const [productIdWarn, setproductIdWarn] = useState(false);
    const [gToken, setgToken] = useState(GetToken());

    const callback = (data) => {
        if (data.status === 200) {
            setshowLoader(false)
            errorMsg('')
            machineSRN('')
            props.dispatchAddProduct(data.data.order);
            props.dispatchUpdateMachineStatus(data.data.product, serialNumber)
        } else {
            setshowLoader(false)
            errorMsg('Something went wrong. Please try again later &#x2639;')
            console.log(data.response)
        }
    }

    const onFormSubmit = (e) => {
        e.preventDefault();
        setshowLoader(true)
        errorMsg('')
        const data = {
            productId: productId,
            name: name,
            version: version,
            machineSRN: machineSRN
        }
        const id = props.unproceedOrder[0]._id;
        if (validate() && id) {
            outlineColor();
            addProduct(callback, id, data, gToken);
        } else {
            outlineColor();
            setshowLoader(false)
            errorMsg(`Select all fields or You can't add same serialNumber.`)
        }
    }

    const onProductSelect = (value) => {
        let filterProduct = props.products.filter((product) => product._id === value);
        if (filterProduct[0] !== undefined && filterProduct[0] !== null) {
            setproductId(value)
            setname(filterProduct[0].name)
        }
    }

    const onMachineSelect = (value) => {
        let filterProduct, filtermachine;
        filterProduct = props.products.filter((product) => product._id === productId);
        if (filterProduct[0] !== undefined && filterProduct[0] !== null) {
            filtermachine = filterProduct[0].productList.filter((machine) => machine.serialNumber === value)
        }
        if (filtermachine) {
            setmachineSRN(value)
            setserialNumber(value)
            setversion(filtermachine[0].version)
        }
    }

    const outlineColor = () => {
        if (!productId.trim()) {
            setproductIdWarn(true)
        } else {
            setproductIdWarn(false)
        } if (!machineSRN.trim()) {
            setserialNumberWarn(true)
        } else {
            setserialNumberWarn(false)
        }
    }

    const validate = () => {
        return version.trim() !== '' && productId.trim() !== '' && machineSRN.trim() !== '' && name.trim() !== '';
    }

    let filterProduct, filtermachine, vendorDisable;

    if (props.unproceedOrder) {
        props.unproceedOrder.length === 0 ? vendorDisable = true : vendorDisable = false;
    } else if (props.unproceedOrder === undefined) {
        vendorDisable = true
    } else {
        vendorDisable = false
    }

    filterProduct = props.products.filter((product) => product._id === productId);
    if (filterProduct[0] !== undefined && filterProduct[0] !== null) {
        filtermachine = filterProduct[0].productList.filter((machine) => machine.sold === false)
    }

    return (
        <Form onSubmitHandler={onFormSubmit}>
            <fieldset disabled={vendorDisable}
                style={vendorDisable ? { pointerEvents: "none", opacity: "0.5", } : {}}
            >
                <legend>Add Products</legend>
                <Dropdown
                    labelName="Product"
                    placeholder="Select Product"
                    optionFilterProp="children"
                    onSelect={onProductSelect}
                    size="large"
                    isRequired={true}
                    filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                    classValue={productIdWarn ? 'inputField-outline' : null}
                >
                    {
                        props.products.map((product) => (
                            <Option value={product._id} key={product._id}>{product.name}</Option>
                        ))
                    }
                </Dropdown>
                <Dropdown
                    labelName="Serial Number"
                    placeholder="Select Serial Number"
                    optionFilterProp="children"
                    onSelect={onMachineSelect}
                    size="large"
                    isRequired={true}
                    filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                    classValue={serialNumberWarn ? 'inputField-outline' : null}
                >
                    {
                        filtermachine !== undefined && filtermachine !== null && (
                            filtermachine.map((machine) => (
                                <Option value={machine.serialNumber} key={machine._id}>{machine.serialNumber}</Option>
                            ))
                        )
                    }
                </Dropdown>
                {errorMsg ? <ErrorBox errorMsgs={errorMsg} /> : null}
                {showLoader ? <Loader>Creating...</Loader> : null}
                <div className="issue-form-button">
                    <Button isType='primary' htmlTypes='submit'>Add</Button>
                </div>
            </fieldset>
        </Form>
    )

}

function mapDispatchToProps(dispatch) {
    return {
        dispatchAddProduct: (data) => {
            dispatch({
                type: actionType.UNPROCEED_ADD_PRODUCT,
                value: data
            })
        },
        dispatchUpdateMachineStatus: (data, serialNumber) => {
            dispatch({
                type: actionType.UPDATE_MACHINE_STATUS,
                value: { data, serialNumber }
            })
        }
    }
}

export default connect(null, mapDispatchToProps)(AddProduct)