

import React, { Component, useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { Modal } from 'antd';
import AddVendor from './add_vendor/add_vendor';
import AddProduct from './add_product/add_product';
import OrderDetail from './order_detail/order_detail';
import Loader from '../../../../components/ui/loader/loader';
import FullScreenLoading from '../../../../components/ui/fullscreen_loader/fullscreen_loader'
//
import { GetToken } from '../../../../helpers/token';
import { getAllProduct } from '../../../../services/api/product_catalog';
import { removeProduct, checkout, removeUnproceedOrder } from '../../../../services/api/orders'
import * as actionType from '../../../../store/actions/action-type'

const AllocateOrder = (props) => {

    const [showLoader, setshowLoader] = useState(props.firstRunProducts);
    const [gToken, setgToken] = useState(GetToken());
    const [orderDetailEditLoader, setorderDetailEditLoader] = useState(false);
    const [errMsg, seterrMsg] = useState('');

    const callback = (data) => {
        if (data.status === 200) {
            setshowLoader(false)
            seterrMsg('')
            props.dispatchAddUnproccedOrder(data.data);
        } else {
            setshowLoader(true)
            seterrMsg('Something went wrong. Please try again later &#x2639;')
            console.log(data.response)
        }
    }
    
    const success = () => {
        Modal.success({
            title: 'Successful',
            content: 'Order has been allocated :).',
        });
    }

    const error = () => {
        Modal.error({
            title: 'Oops',
            content: 'Something went wrong. Please try again later :(',
        })
    }

    const productCallback = (data) => {
        if (data.status === 200) {
            props.getProducts(data.data);
            setshowLoader(false)
            props.dispatchUpdateFirstRunProducts(false);
        } else {
            error();
            console.log(data.response)
        }
    }

    const removeProductCallback = (data, serialNumber, id) => {
        if (data.status === 200) {
            setorderDetailEditLoader(false)
            props.dispatchUnproceedRemoveProduct(data.data.order, id);
            props.dispatchUpdateMachineStatus(data.data.product, serialNumber);
        } else {
            setorderDetailEditLoader(false)
            error();
            console.log(data.response)
        }
    }

    const removeProduct = (data, id) => {
        setorderDetailEditLoader(true)
        if (gToken) {
            removeProduct(removeProductCallback, id, data, gToken)
        }
    }

    const removeOrderCallback = (data) => {
        if (data.status === 200) {
            setorderDetailEditLoader(false)
            props.dispatchRemoveOrder(data.data)
        } else {
            setorderDetailEditLoader(false)
            error();
            console.log(data.response)
        }
    }

    const removeOrder = (id) => {
        setorderDetailEditLoader(true)
        if (gToken) {
            removeUnproceedOrder(removeOrderCallback, id, gToken)
        }
    }

    const proceedCallback = (data) => {
        if (data.status === 200) {
            setorderDetailEditLoader(false)
            props.dispatchProceedOrder(data.data);
            success();
        } else {
            setorderDetailEditLoader(false)
            error();
            console.log(data.response)
        }
    }

    const proceedOrder = (id) => {
        setorderDetailEditLoader(true)
        if (gToken) {
            checkout(proceedCallback, id, gToken)
        }
    }

    useEffect(() => {
        if (gToken) {
            if (props.firstRunProducts) {
                getAllProduct(callback, gToken);
            }
        }
    },[])

    if (showLoader) {
        return <Loader />
    } else {
        return (
            <div className="container flex-row">
                {
                    orderDetailEditLoader
                        ? <FullScreenLoading />
                        : null
                }
                <div className="container-left">
                    <AddVendor companies={props.companies} unproceedOrder={props.unproceedOrder} />
                    <AddProduct products={props.products} unproceedOrder={props.unproceedOrder} />
                </div>
                <div className="container-right">
                    <div className="order-detail-container">
                        <div className="order-detail" style={{ overflowX: 'auto' }}>
                            <OrderDetail
                                Orders={props.Orders}
                                proceedOrder={proceedOrder}
                                removeProduct={removeProduct}
                                companies={props.companies}
                                removeOrder={removeOrder}
                            />
                        </div>
                    </div>
                </div>
            </div>
        )
    }

}

function mapStateToProps(state) {
    return {
        products: state.Products.products,
        firstRunProducts: state.Products.firstRun
    }
}

function mapDispatchToProps(dispatch) {
    return {
        getProducts: (data) => {
            dispatch({
                type: actionType.PRODUCTS,
                value: data
            })
        },
        dispatchUpdateMachineStatus: (data, serialNumber) => {
            dispatch({
                type: actionType.UPDATE_MACHINE_STATUS,
                value: { data, serialNumber }
            })
        },
        dispatchUnproceedRemoveProduct: (data, id) => {
            dispatch({
                type: actionType.UNPROCEED_REMOVE_PRODUCT,
                value: { data, id }
            })
        },
        dispatchProceedOrder: (data) => {
            dispatch({
                type: actionType.UPDATE_ORDER_STATUS,
                value: data
            })
        },
        dispatchRemoveOrder: (data) => {
            dispatch({
                type: actionType.REMOVE_UNPROCEED_ORDER,
                value: data
            })
        },
        dispatchUpdateFirstRunProducts: (data) => {
            dispatch({
                type: actionType.UPDATE_FIRST_RUN_PRODUCTS,
                value: data
            })
        }
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(AllocateOrder)