

import React, { Component, useEffect, useState } from 'react';
import { connect } from 'react-redux'
import { Tabs, Modal } from 'antd';
import Icon from '@ant-design/icons';

import AllocateOrder from './allocate_order/allocate_order';
import OrderList from './order_list/order_list';
import ChangeStatus from './change_status/change_status'
import Loader from '../../../components/ui/loader/loader'
import './orders.css'
//
import * as actionType from '../../../store/actions/action-type'
import getCompanylist from '../../../services/api/get-company';
import { GetToken } from '../../../helpers/token'
import { getOrders, changeStatus } from '../../../services/api/orders'

const TabPane = Tabs.TabPane;

const Orders = (props) => {

    const [gToken, setGToken] = useState(GetToken());
    const [showPLoader, setShowPLoader] = useState(props.firstRunProducts);
    const [showOLoader, setShowOLoader] = useState(props.firstRunOrders);
    const [showCLoader, setShowCLoader] = useState(props.firstRunCompanies);



    const success = () => {
        Modal.success({
            title: 'Successful',
            content: 'Order status has been changed :).',
        });
    }

    const error = () => {
        Modal.error({
            title: 'Oops',
            content: 'Something went wrong. Please try again later :(',
        })
    }

    const callback = (data) => {
        if (data.status === 200) {
            props.dispatchOrders(data.data);
            props.showLoader(false);
            props.dispatchUpdateFirstRunOrders(false);
        }
    }

    const changeStatusCallback = (data, status) => {
        if (data.status === 200) {
            if (status === 'installed') {
                props.dispatchChangeOrderStatus(data.data.updatedOrder);
                props.dispatchNewVendorData(data.data.newVendorData);
            } else {
                props.dispatchChangeOrderStatus(data.data)
            }
            success()
        } else {
            error();
            console.log(data.response)
        }
    }

    const changeOrderStatus = (status, id) => {
        if (gToken) {
            changeStatus(changeStatusCallback, id, { status: status }, gToken)
        }
    }

    const companyCallback = (data) => {
        if (data.status === 200) {
            props.getCompany(data.data);
            props.showLoader(false);
            props.dispatchUpdateFirstRunCompanies(false);
        } else {
            console.log(data.response);
        }
    }

    useEffect(() => {
        if (gToken) {
            if (props.firstRunOrders) {
                getOrders(callback, gToken);
            }
            if (props.firstRunCompanies) {
                getCompanylist(companyCallback, gToken);
            }
        }
    }, [])



    // for unproceed order
    let unproceedOrder;
    if (props.orders.length > 0) {
        unproceedOrder = props.orders.filter((orders) => orders.orderStatus.allocated === false);
    }

    if (showOLoader && showCLoader && !props.orders && !props.companies) {
        return <Loader />
    } else if (props.orders) {
        return (
            <Tabs defaultActiveKey="1" >
                <TabPane tab={<span><Icon type="dashboard" />Dashboard</span>} key="1" >
                    Orders Dashboard
                    </TabPane>
                <TabPane tab={<span><Icon type="ordered-list" />Order List</span>} key="2">
                    <OrderList Orders={props.orders} companies={props.companies} />
                </TabPane>
                <TabPane tab={<span><Icon type="edit" />Change Status</span>} key="3">
                    <ChangeStatus Orders={props.orders} changeOrderStatus={changeOrderStatus} companies={props.companies} />
                </TabPane>
                <TabPane tab={<span><Icon type="form" />Allocate Order</span>} key="4">
                    <AllocateOrder
                        Orders={props.orders}
                        unproceedOrder={unproceedOrder}
                        companies={props.companies}
                    />
                </TabPane>
            </Tabs>
        )
    }

}

function mapStateToProps(state) {
    return {
        firstRunOrders: state.Orders.firstRun,
        orders: state.Orders.orders,
        firstRunCompanies: state.Company.firstRun,
        companies: state.Company.company,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        getCompany: (data) => {
            dispatch({
                type: actionType.COMPANY,
                value: data
            })
        },
        dispatchOrders: (data) => {
            dispatch({
                type: actionType.ORDERS,
                value: data
            })
        },
        dispatchChangeOrderStatus: (data) => {
            dispatch({
                type: actionType.UPDATE_ORDER_STATUS,
                value: data
            })
        },
        dispatchNewVendorData: (data) => {
            dispatch({
                type: actionType.ADD_VENDOR_PRODUCTS,
                value: data
            })
        },
        dispatchUpdateFirstRunOrders: (data) => {
            dispatch({
                type: actionType.UPDATE_FIRST_RUN_ORDERS,
                value: data
            })
        },
        dispatchUpdateFirstRunCompanies: (data) => {
            dispatch({
                type: actionType.UPDATE_FIRST_RUN_COMPANY,
                value: data
            })
        }
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(Orders)