

import React, { Component, useState } from 'react';
import { connect } from 'react-redux'
import { Select } from 'antd'
import Form from '../../../../../components/form/form';
import Dropdown from '../../../../../components/form/form-controls/select';
import Button from '../../../../../components/form/button/button';
import Loader from '../../../../../components/ui/loader/loader';
import ErrorBox from '../../../../../components/form/error-box/error-box';
//
import * as actionType from '../../../../../store/actions/action-type'
import { GetToken } from '../../../../../helpers/token';
import { addVendor } from '../../../../../services/api/orders';

const Option = Select.Option;

const AddVendor = (props) => {

    const [vendorId, setvendorId] = useState('');
    const [errorMsg, seterrorMsg] = useState(false);
    const [showLoader, setshowLoader] = useState(false);
    const [vendorIdWarn, setvendorIdWarn] = useState(false);
    const [gToken, setgToken] = useState(GetToken());


    const callback = (data) => {
        if (data.status === 200) {
            setshowLoader(false)
            seterrorMsg('')
            props.dispatchAddUnproccedOrder(data.data);
        } else {
            setshowLoader(true)
            seterrorMsg('Something went wrong. Please try again later &#x2639;')
            console.log(data.response)
        }
    }

    const onFormSubmit = (e) => {
        e.preventDefault();
        setshowLoader(true)
        seterrorMsg('')
        if (validate()) {
            outlineColor();
            addVendor(callback, { vendorId: vendorId }, gToken);
        } else {
            outlineColor();
            setshowLoader(false)
            seterrorMsg('Please Select Vendor/Company.')
        }
    }

    const onVendorSelect = (value) => {
        setvendorId(value)
    }

    const outlineColor = () => {
        if (!vendorId.trim()) {
            setvendorIdWarn(true)
        } else {
            setvendorIdWarn(false)
        }
    }

    const validate = () => {
        return vendorId.trim() !== '';
    }

    let vendorDisable;
    if (props.unproceedOrder) {
        props.unproceedOrder.length > 0 ? vendorDisable = true : vendorDisable = false
    }

    return (
        <Form onSubmitHandler={onFormSubmit}>
            <fieldset
                disabled={vendorDisable}
                style={vendorDisable ? { pointerEvents: "none", opacity: "0.5", } : {}}
            >
                <legend>Add Vendor</legend>
                <Dropdown
                    labelName="Vendor/Company"
                    placeholder="Select Vendor"
                    optionFilterProp="children"
                    onSelect={onVendorSelect}
                    size="large"
                    isRequired={true}
                    filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                    classValue={vendorIdWarn ? 'inputField-outline' : null}
                >
                    {
                        props.companies.map((company) => (
                            <Option value={company._id} key={company._id}>{company.name}</Option>
                        ))
                    }
                </Dropdown>
                {errorMsg ? <ErrorBox errorMsgs={errorMsg} /> : null}
                {showLoader ? <Loader>Adding...</Loader> : null}
                <div className="issue-form-button">
                    <Button isType='primary' htmlTypes='submit'>Add</Button>
                </div>
            </fieldset>
        </Form>
    )

}

function mapDispatchToProps(dispatch) {
    return {
        dispatchAddUnproccedOrder: (data) => {
            dispatch({
                type: actionType.ADD_UNPROCEED_ORDER,
                value: data
            })
        }
    }
}

export default connect(null, mapDispatchToProps)(AddVendor)